<template>
    <nav class="nav">
        <item-button v-on:click="handleNew"
                     v-bind:disabled="disabledNew || !disabledSave">
            New
        </item-button>
        <item-button v-on:click="handleSave"
                     v-bind:disabled="disabledSave">
            Save
        </item-button>
        <item-button v-on:click="handleDelete"
                     v-bind:disabled="disabledDelete">
            Delete
        </item-button>

        <!--  <router-link to="/Settings" class="nav__justify-right link-icon">
            <svg class="svg svg--settings"
                 width="24"
                 height="24"
                 viewBox="0 0 24 24">
                <use class="svg__use"
                     xlink:href="#settings"></use>
            </svg>
        </router-link> -->
    </nav>
</template>

<script>
import ItemButton from '../items/Button.vue';

export default {
    name: 'component-navigation',

    components: {
        ItemButton
    },

    data () {
        return {
            disabledNew: false
        };
    },

    mounted () {
        this.disabledNew = this.$route.name === 'pattern-new';
    },

    computed: {
        disabledSave () {
            const activePattern = this.$store.getters['pattern/active/pattern'];
            const newPattern = this.$store.getters['pattern/new/pattern'];

            if (this.disabledNew) {
                const initName = newPattern.name === '';

                if (initName) {
                    return true;
                } else {
                    return false;
                }
            } else {
                const initText = activePattern.text === newPattern.text;

                let activeCodeContent = '';
                let newCodeContent = '';

                if (activePattern.code.length) {
                    activeCodeContent = activePattern.code[0].content;
                }

                if (newPattern.code.length) {
                    newCodeContent = newPattern.code[0].content;
                }

                const initCode = activeCodeContent === newCodeContent;

                if (initText && initCode) {
                    return true;
                } else {
                    return false;
                }
            }
        },

        disabledDelete () {
            if (typeof this.$route.params.id !== 'undefined') {
                return false;
            }
            return true;
        }
    },

    methods: {
        handleTest () {
            console.log('test button clicked');
        },

        handleNew () {
            this.$router.push({ path: '/new' });
        },

        async handleSave () {
            const newPattern = this.$route.name === 'pattern-new';

            if (newPattern) {
                const url = await this.$store.dispatch('pattern/new/saveNew');
                this.$router.push({ path: `/${url}` });
            } else {
                await this.$store.dispatch('pattern/new/saveActive');
            }
        },

        handleDelete () {
            this.$store.dispatch('modal/openDelete');
        }
    }
};
</script>

