<template>
    <aside class="aside">
        <p class="aside__label">Patterns</p>
        <ul class="aside__list">
            <li v-for="pattern in patterns" :key="pattern.id">
                <router-link :to="{ path: '/' + pattern.url }">{{pattern.name}}</router-link>
            </li>
            <form class="form" autocomplete="off" v-on:submit.prevent>
                <input
                    v-if="newPattern"
                    id="inputNewPatternName"
                    class="input"
                    v-bind:class="{ 'input--error': inputError.display }"
                    ref="inputNewPatternName"
                    type="text"
                    v-on:input="nameInputHandler"
                    v-model="inputPatternName"
                    autocomplete="off"
                    autofocus
                >
                <label
                    v-if="newPattern && inputError.display && inputError.empty"
                    for="inputNewPatternName"
                    class="label label--error"
                >Filename can not be empty. Please choose an unique file name.</label>
                <label
                    v-if="newPattern && inputError.display && inputError.usedName"
                    for="inputNewPatternName"
                    class="label label--error"
                >Please choose an unique file name.</label>
            </form>
        </ul>
    </aside>
</template>

<script>
export default {
    name: 'component-aside',

    data () {
        return {
            inputPatternName: null,
            inputError: {
                display: false,
                empty: false,
                usedName: false
            }
        };
    },

    async mounted () {
        await this.getAllByCreated();
    },

    computed: {
        patterns () {
            return this.$store.getters['pattern/list/all'];
        },

        newPattern () {
            return this.$route.path === '/new';
        }
    },

    methods: {
        newHandler () {
            this.$router.push({ path: '/new' });
        },

        async getAllByCreated () {
            await this.$store.dispatch('pattern/list/setPatterns');
        },

        async nameInputHandler () {
            // trim whitespace at start
            this.inputPatternName = this.inputPatternName.replace(/^\s+/g, '');

            let trimmedPatternName = this.inputPatternName.replace(/\s+$/g, '');

            if (!this.inputPatternName || this.inputPatternName === '') {
                this.inputError.display = true;
                this.inputError.empty = true;
            } else {
                const nameAvailable = await this.$store
                    .getters['pattern/new/nameAvailable'](trimmedPatternName);

                if (!nameAvailable) {
                    this.inputError.display = true;
                    this.inputError.usedName = true;

                    trimmedPatternName = '';
                } else {
                    this.inputError.display = false;
                    this.inputError.empty = false;
                    this.inputError.usedName = false;
                }
            }

            await this.$store.dispatch('pattern/new/setName', {
                // trim whitespace at end
                name: trimmedPatternName
            });
        }
    }
};
</script>

