<template>
    <button v-bind:type="type"
            v-on:click="handleClick">
        <slot></slot>
    </button>
</template>

<script>
export default {
    name: 'item-button',

    props: {
        type: {
            type: String,
            default: 'text'
        }
    },

    methods: {
        handleClick () {
            this.$emit('click');
        }
    }
};
</script>

