<template>
    <dialog class="modal absolute"
            v-if="display">
        <div class="modal__box flex flex--vertical"
             v-if="typeDelete">
            <div class="modal__box__right">
                <item-button v-on:click="handleCancel">
                    Cancel
                </item-button>
                <item-button v-on:click="handleDelete">
                    Delete
                </item-button>
            </div>
            <div class="modal__box__left">
                <b>Do you want to delete this pattern?</b>
                <p>This Pattern will be lost and can´t be restored!</p>
            </div>
        </div>
    </dialog>
</template>

<script>
import ItemButton from '../items/Button.vue';

export default {
    name: 'component-modal',

    components: {
        ItemButton
    },

    data () {
        return {
            closing: false,
            htmlCode: '',
            cssCode: '',
            name: ''
        };
    },

    computed: {
        display () {
            return this.$store.getters['modal/display'];
        },

        typeDelete () {
            return this.$store.getters['modal/typeDelete'];
        }
    },

    methods: {
        async handleCancel () {
            await this.$store.dispatch('modal/close');
        },

        async handlerDelete () {
            await this.$store.dispatch('pattern/active/deletePattern');
            await this.$store.dispatch('modal/close');
            this.$router.push({ path: '/' });
        }
    }
};
</script>

